import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/components/Layout'

Vue.use(VueRouter)
/**
 * isHidden: 是否隐藏
 * isDirect: 是否目录，还是有详细内容
 */
const routes = [
  {
    path: '/login',
    component: () => import('@/views/login/index'),
    isHidden: true,
    meta: { title: '登录', icon: 'login' }
  },
  {
    path: '/error',
    component: Layout,
    isHidden: true,
    children: [
      {
        path: '/404',
        component: () => import('@/views/error/404'),
        meta: { title: '404', icon: '404'}
      }
    ]
  },  
  
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [{
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      path: 'home',
      name: 'Home',
      component: () => import('@/views/home/index'),
      meta: { title: '主页', icon: 'el-icon-menu' }
    },
    {
      path: 'setting',
      name: 'Setting',
      isHidden: true,
      component: () => import('@/views/setting/index'),
      meta: { title: '设置', icon: 'setting' }
    },
    {
      path: 'userinfo',
      name: 'Userinfo',
      isHidden: true,
      component: () => import('@/views/userinfo/index'),
      meta: { title: '用户信息', icon: 'userinfo' }
    }]
  },
  {
    path: '/organization',
    component: Layout,
    meta: { title: '组织机构管理', icon: 'el-icon-menu', isDirect: true},
    children: [{
      path: 'usermanage',
      name: 'UserManage',
      component: () => import('@/views/organization/usermanage/index'),
      meta: { title: '用户管理'}
    },
    {
      path: 'roles',
      name: 'Roles',
      component: () => import('@/views/organization/roles'),
      meta: { title: '角色管理'}
    }]
  },
  {
    path: '/authmanage',
    name: 'AuthManage',
    component: Layout,
    meta: { title: '权限管理', icon: 'el-icon-menu', isDirect: true},
    children: [{
      path: 'service',
      name: 'Service',
      component: () => import('@/views/authmanage/service'),
      meta: { title: '服务管理'}
    },
    {
      path: 'auth',
      name: 'Auth',
      component:() => import('@/views/authmanage/auth'),
      meta: { title: '权限功能管理'}
    },
    {
      path: 'resource',
      name: 'Resource',
      component:() => import('@/views/authmanage/resource'),
      meta: { title: '资源管理'}
    }]
  },
  {
    path: '/logsmanage',
    name: 'LogsManage',
    component: Layout,
    meta: { title: '日志管理', icon: 'el-icon-menu', isDirect: true},
    children: [{
      path: 'operationlogs',
      name: 'OperationLogs',
      component: () => import('@/views/logsmanage/operationlogs'),
      meta: { title: '操作日志'}
    },
    {
      path: 'visitlogs',
      name: 'VisitLogs',
      component:() => import('@/views/logsmanage/visitlogs'),
      meta: { title: '访问日志'}
    }]
  },
  {
    path: '/systemmanage',
    name: 'SystemManage',
    component: Layout,
    meta: { title: '系统管理', icon: 'el-icon-menu', isDirect: true},
    children: [{
      path: 'onlineuser',
      name: 'OnlineUser',
      component: () => import('@/views/systemmanage/onlineuser'),
      meta: { title: '在线用户'}
    },
    {
      path: 'cache',
      name: 'Cache',
      component:() => import('@/views/systemmanage/cache'),
      meta: { title: '缓存管理'}
    },
    {
      path: 'safe',
      name: 'Safe',
      component:() => import('@/views/systemmanage/safe'),
      meta: { title: '安全设置'}
    }]
  },
  {
    path: '/about',
    component: Layout,
    children: [{
      path: 'index',
      name: 'About',
      component: () => import('@/views/about/index'),
      meta: { title: '关于', icon: 'el-icon-menu' }
    }]
  },
]

const router = new VueRouter({
  scrollBehavior: () => ({ y: 0 }),
  mode: 'history',  //不加这个，输入任何页面都会跳到首页
  base: '/',
  routes
})

export default router
