<template>
  <div v-if="!item.isHidden">
    <!-- MenuItem -->
    <!-- 普通导航 -->
    <div v-if="hasOneChildren(item.children,item)">
      <router-link v-if="childItem" :to="childItem.path" >
        <el-menu-item :index="childItem.path">
          <Item v-if="childItem.meta" :icon='childItem.meta.icon' :title="childItem.meta.title"></item>
        </el-menu-item>
      </router-link>
    </div>
    <!-- 下拉导航 -->
    <el-submenu v-else :index="item.path">
      <template slot="title">
        <item v-if="item.meta" :icon='item.meta.icon' :title="item.meta.title"></item>
      </template>
      <menu-item v-for="route in item.children" :key="route.path" :item='route'></menu-item>
    </el-submenu>
  </div>
</template>

<script>
import path from 'path'
import Item from './Item'

export default {
  name: "MenuItem",
  components: {
    Item
  },
  props: {
    item:{
      type: Object,
      required: true
    }
  },
  data() {
    //子对象，单个显示的item
      this.childItem= null
    return {
      
    };
  },
  watch: {},
  computed: {},
  methods: {
    hasOneChildren(children = [],current){
      //是否有子路由,或者一个子路由,如果只有一个子路由让他直接显示
      //过滤需要隐藏的
      let child = children.filter(item => {
        if(item.isHidden) return false;
        else {
          //没有被过滤掉的只会有一个，所以变成子对象
          this.childItem = item;
          //替换成绝对路径
          this.childItem.path = path.resolve(current.path,item.path);
          return true;
        }
      })
      // debugger
      if(child.length===0){  
        //没有子路由，使用当前路由
        this.childItem = current;
        return true
      }
      else if(child.length===1){
        // this.childItem
        return true
      }
      return false
    }
  }
};
</script>
<style lang="scss">
.el-submenu .el-submenu__title{
  padding-left: 20px !important; 
}
.el-submenu .el-menu-item:hover{
  background-color: #001528 !important;
}
</style>