import elementVariables from "@/styles/element-variables.scss"
import skin from "@/styles/variables.scss"

//主题颜色

const state = {
    colorPrimary: elementVariables.theme,
    skin: skin.skin,
}

const mutations = {
    CHANGE_SETTING: (state, {key,value}) => {        
        if(Object.prototype.hasOwnProperty.call(state, key)){
            state[key] = value;
        }
    }
} 

const actions = {
    changeSetting({commit},data) {
        commit('CHANGE_SETTING', data)
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
  }